































import {Component, Vue} from 'vue-property-decorator';
import MFrame from "@/components/MFrame.vue";
import chatType from "@/utils/ChatType";
import toast from "@/utils/Toast";

@Component({
    components: {MFrame}
})
export default class NewGp extends Vue {
    chatType = chatType
    private group_name: string = "";
    private subscribed: string[] = [];

    create(evt: any) {
        evt.preventDefault();
        try {
            this.$store.commit('new_group', {name: this.group_name, subscribe: this.subscribed.map(x => +x)});
            this.$router.push({name: 'Home',})

        } catch (e) {
            toast(e, "error")
        }
    }
}
